import React, { Fragment, useState } from 'react'
import Chart from "react-apexcharts";
import classes from './main.module.css'
function AverageChart() {
    const [chartoptions, setchartOptions] = useState({
        plotOptions: {
            bar: {
                horizontal: false,
                s̶t̶a̶r̶t̶i̶n̶g̶S̶h̶a̶p̶e̶: 'flat',
                e̶n̶d̶i̶n̶g̶S̶h̶a̶p̶e̶: 'flat',
                borderRadius: 0,
                columnWidth: '25%',
                barHeight: '70%',
                distributed: false,
                rangeBarOverlap: true,
                rangeBarGroupRows: false        
            }
        },
        legend: {
            position: 'bottom',
            fontSize: '16px',
            fontFamily: 'Source Sans Pro',
            labels: {
                position: 'front',
                colors: '#aaa',
            },
        },
        grid: {
            show: true,
            borderColor: '#90A4AE',
            row: {
                colors: '#fff',
                opacity: 1
            },
        },
        tooltip: {
            shared: false,
            intersect: false
        },
        chart: {
            type: 'bar',
            height: 450,
            stacked: true,
            background: '#fff'
           
        },
        dataLabels: {
            enabled: false,
        },
        title: {
            text: 'Average Testosterone Levels by Age',
            align: 'center',
            margin: 10,
            offsetX: 0,
            offsetY: 0,
            floating: false,
            style: {
                fontSize: '20px',
                fontWeight: 'bold',
                fontFamily: 'Source Sans Pro',
                color: '#A8A8A8'
            }
        },
        yaxis: {
            tickAmount: 10,
            min: 0,
            max: 18,
            forceNiceScale: true,
            decimalsInFloat: 0,
            labels: {
                show: true,
                align: 'right',
                minWidth: 0,
                maxWidth: 160,
                style: {
                    colors: '#000',
                    fontSize: '20px',
                    fontFamily: 'Source Sans Pro',
                    fontWeight: 400,
                    cssClass: 'apexcharts-yaxis-label',
                },
                offsetX: 0,
                offsetY: 0,
                rotate: 0,
            },
        },
        xaxis: {
            min: 0,
            max: 6,
                categories: ['31-40', '41-50', '51-60', '60-70'
                ],
                labels: {
                    style: {
                      colors: '#000',
                      fontSize: '20px',
                      fontFamily: 'Source Sans Pro',
                      fontWeight: 400,
                      cssClass: 'apexcharts-yaxis-label',
                    },
                  },
        },
        stroke: {
            show: true,
            curve: 'smooth',
        },
        colors: ['#5B9BD5', '#ED7D31', '#EC3641'],
        responsive: [
            {
              breakpoint: 576,
              options: {
                title: {
               
                 
                  margin: 20,
                  offsetX: 0,
                  offsetY: 0,
                  floating: false,
                  style: {
                    fontSize: '12px',
                    fontWeight: 'bold',
                    fontFamily: 'Source Sans Pro',
                    color: '#A8A8A8'
                  }
                },
              }
            }
          ]
    },
    
    )
    // series start
    const [chartSeries, setchartSeries] = useState([{
        name: 'Average Testosterone Levels',
        data: [7,6,5,4 ]
    }, {
        name: 'With Other Testosterone Supplements',
        data: [2, 1, 1, 1]
    }, {
        name: 'With  Platinum Pro',
        data: [7,7,7,7]
    }],
    )
    // series end
    return (
        <div class={`${classes.average_chart}`}>
            <Chart options={chartoptions} series={chartSeries} type="bar" height={450} />
        </div>
    )
}
export default AverageChart